import { TSSliceQueue } from '../struct'
import { MpegtsContext, PID } from '../type'
import parsePAT from './parsePAT'
import parsePMT from './parsePMT'
import * as mpegts from '../mpegts'
import parseSCTE35 from './parseSCTE35'

export default function parseSection(pid: PID, queue: TSSliceQueue, mpegtsContext: MpegtsContext) {
  if (pid === 0x00) {
    parsePAT(queue, mpegtsContext)
  }
  else if (pid === mpegtsContext.currentPmtPid) {
    parsePMT(queue, mpegtsContext)
  }
  else if (mpegtsContext.pmt && mpegtsContext.pmt.pid2StreamType.get(pid) === mpegts.TSStreamType.kSCTE35) {
    parseSCTE35(queue, mpegtsContext)
  }
}
