import { NOPTS_VALUE } from 'avutil/constant'
import { PAT, PMT } from '../struct'
import { MpegtsContext } from '../type'

export default function createMpegtsContext(): MpegtsContext {

  return {
    currentProgram: NOPTS_VALUE,
    currentPmtPid: NOPTS_VALUE,
    tsPacketSize: NOPTS_VALUE,
    hasPAT: false,
    hasPMT: false,
    tsSliceQueueMap: new Map(),
    pat: new PAT(),
    pmt: new PMT(),
    program2Pmt: new Map(),
    ioEnd: false,

    startPid: 0x100,
    delay: 0n
  }
}
